/* eslint-disable no-restricted-imports */
import React, {
    useEffect
} from 'react';
import $ from 'jquery';
import 'datatables.net-bs4';
import {
    Portlet,
    PortletBody,
    PortletHeader,
    PortletHeaderToolbar
} from "../../partials/content/Portlet";
import API from '../API'
import DialogReason from './Utils/DialogReason'
import DialogHapusBatal from './Utils/DialogHapusBatal'
import { AlertList} from "react-bs-notifier";
// import Utils from './Utils/FunctionDetail'


export default function ReasonBatal() {
    const [open, setOpen] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [id, setId] = React.useState(null);
    const [idhapus, setIdhapus] = React.useState(null);
    const [alerts, setAlerts] = React.useState([]);

    const handleClickOpen = () => {
        setId(null)
        setOpen(true);
        $('#alasanaja').val('')
        $('#cancel_type').val('pilih')
        $('#role_type').val('pilih')
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleClickOpen2 = () => {
        setOpen2(true);
    };

    const handleClose2 = () => {
        setOpen2(false);
    };

    const HapusReasonBatal = () => {
        var settings = {
            "async": true,
            "crossDomain": true,
            "url": API.Utilsapi + "v1/reason/delete/" + idhapus,
            "method": "GET",
            "headers": {
                "Authorization": "Baisc " + localStorage.getItem('jwt')
            }
        }

        $.ajax(settings).done(function (response) {
            handleClose2()
            setAlerts([{
                id: 1,
                type: "success",
                headline: "Berhasil",
                message: "Reason Batal Telah Dihapus"
            }])
            setTimeout(() => {
                setAlerts([])
            }, 3000)
            return 0
        }).fail(function (response) {
            console.log(response)
            handleClose2()
            setAlerts([{
                id: 1,
                type: "danger",
                headline: "Gagal",
                message: "Terjadi Kendala [Error Code " + response.status + "]"
            }])
            setTimeout(() => {
                setAlerts([])
            }, 3000)
            return 0
        })
    };

    useEffect(() => {
        $(document).ready(function () {
            $('#cancel_type').attr('disabled',true)
            $(document).on('change', '#role_type', function () {
                var mrsn_role = $("#role_type").val();
                $('#cancel_type').removeAttr('disabled')
                // eslint-disable-next-line eqeqeq
                if(mrsn_role == "mitra"){
                  $("#pembatalan_menunggu_mitra").hide();
                  $("#pembatalan_validasi").show();
                  $("#pembatalan_surat_tugas").show();
                }else{
                  $("#pembatalan_surat_tugas").hide();
                  $("#pembatalan_menunggu_mitra").show();
                  $("#pembatalan_validasi").show();
                }
                });

            $('#reason').DataTable({
                "pagingType": "full_numbers",
                "processing": true,
                "serverSide": true,
                "destroy": true,
                "ajax": {
                    "url": API.Utilsapi + "/v1/reason/index",
                    "type": 'get',
                    "beforeSend": function (xhr) {
                        xhr.setRequestHeader('Authorization',
                            "Basic " + localStorage.getItem('jwt'));
                    },
                    error: function (xhr, error, thrown, settings) {
                        // errorData(xhr);
                    }
                },
                "columns": [
                    {
                        sortable: true,
                        "render": function ( data, type, full, meta ) {
                          return " <div >"+full.mrsn_role+"</div>";
                            // return " <div >"+getTypeMrsn(full.mrsn_type)+"</div>";
                        }
                    },
                    {
                        sortable: true,
                        "render": function (data, type, full, meta) {
                            return " <div >" + full.mrsn_type + "</div>";
                            // return " <div >"+getTypeMrsn(full.mrsn_type)+"</div>";
                        }
                    },
                    {
                        "data": "mrsn_text"
                    },
                    {
                        sortable: true,
                        "render": function (data, type, full, meta) {
                            return " <div >" + full.mrsn_status + "</div>";
                            // return " <div >"+Utils.StatusUser(full.mrsn_status)+"</div>";
                        }
                    },
                    {
                        sortable: true,
                        "render": function (data, type, full, meta) {
                            return (
                                `<div>
                                <button type='button' class='edit btn btn-outline-brand btn-icon btn-sm mr-1 mb-1' id='edit' data-id=${full.mrsn_id} data-original-title='Edit User'><i class='fa fa-edit'></i></button>
                                <button type='button' class='btn btn-outline-brand btn-icon btn-sm mr-1 mb-1' id='hapus' data-id=${full.mrsn_id} data-original-title='Hapus User'><i class='fa fa-trash'> </i></button>
                            </div>`)
                        }
                    },
                ]
            });

            $(document).on('click', '#edit', function () {
                handleClickOpen()
                setId($(this).attr('data-id'))
                var settings = {
                    "async": true,
                    "crossDomain": true,
                    "url": API.Utilsapi + "v1/reason/edit/" + $(this).attr('data-id'),
                    "method": "GET",
                    "headers": {
                        "Authorization": "Baisc " + localStorage.getItem('jwt')
                    }
                }
                $.ajax(settings).done(function (response) {
                    $("#cancel_type").val(response.data.mrsn_type);
                    $("#alasanaja").val(response.data.mrsn_text);
                    $("#role_type").val(response.data.mrsn_role);
                    console.log(response)
                })
            })

            $(document).on('click', '#hapus', function () {
                handleClickOpen2()
                setIdhapus($(this).attr('data-id'))
            })

            $('.dataTables_filter,.dataTables_paginate').addClass('pull-right')
        })
    })

    const SubmitBatal = () => {

        var data = {
            'mrsn_type': $('#cancel_type').val(),
            'mrsn_text': $('#alasanaja').val(),
            'mrsn_role': $('#role_type').val()
        };
        console.log(data)
        console.log($('#alasanaja').val())
        var url
        if (id != null) {
            url = API.Utilsapi + "v1/reason/update/" + id;
        } else {
            url = API.Utilsapi + "v1/reason/add";
        }

        var settings = {
            "async": true,
            "crossDomain": true,
            "url": url,
            "method": "POST",
            "headers": {
                "Content-Type": "application/json",
                "Authorization": "Baisc " + localStorage.getItem('jwt')
            },
            "processData": false,
            "data": JSON.stringify(data)
        }

        $.ajax(settings).done(function (response) {
            setAlerts([{
                id: 1,
                type: "success",
                headline: "Berhasil",
                message: id!=null?"Reason Batal Telah Diubah":"Reason Batal Telah Ditambahkan"
            }])
            setTimeout(() => {
                setAlerts([])
            }, 3000)
            handleClose()
            return 0
        }).fail(function (response) {
            console.log(response)
            setAlerts([{
                id: 1,
                type: "danger",
                headline: "Gagal",
                message: "Terjadi Kendala [Error Code " + response.status + "]"
            }])
            setTimeout(() => {
                setAlerts([])
            }, 3000)
            return 0
        })
    }

    return(
        <>  
            <AlertList  showIcon={false} alerts={alerts}/>
            <DialogReason 
                open={open} 
                close={handleClose} 
                title={id!=null?"Edit Reason Batal":"Tambah Reason Batal"} 
                nego="none" 
                submit={SubmitBatal}
            />
            <DialogHapusBatal 
                open={open2} 
                close={handleClose2} 
                title="Hapus Reason Batal" 
                message="Anda Yakin Ingin Hapus Reason Ini ?" 
                btn="Hapus" 
                fun={HapusReasonBatal}
                // type="delete" 
                // id={idhapus}
            />
            <Portlet>
                <PortletHeader
                    title="Data Reason Batal"
                    toolbar={(
                        <PortletHeaderToolbar>
                            <button
                                type="button"
                                className="btn btn-label-primary"
                                onClick={handleClickOpen}
                            >
                                <i class="fas fa-plus"></i>Tambah Reason Batal
                            </button>
                        </PortletHeaderToolbar>
                    )}
                >
                </PortletHeader>
                <PortletBody>
                    <table className="table table-hover table-responsive-md" id="reason" style={{width:'100%'}}>
                            <thead className="bg-primary" style={{color:'white'}}>
                                <tr>
                                <th scope="col">Role</th>
                                <th scope="col">Tipe</th>
                                <th scope="col">Alasan</th>
                                <th scope="col">Status</th>
                                <th scope="col">Aksi</th>
                                </tr>
                            </thead>
                    </table>
                </PortletBody>
            </Portlet>
        </>
    )
}