export default function UploadData() {
    const [open, setOpen] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [id, setId] = React.useState(null);

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClose() {
        setOpen(false);
    }

    function handleClickOpen2() {
        setOpen2(true);
    }

    function handleClose2() {
        setOpen2(false);
    }

     useEffect(() => {
         $(document).ready(function () {

            $('#dp_terdaftar').DataTable( {
                "pagingType": "full_numbers",
                "processing": true,
                "serverSide": true,
                "destroy": true,
                "ajax": {
                    "url": API.Userapi+"v1/Droppoint/dpTerdaftar",
                    "type": 'get',
                    "beforeSend": function (xhr) {
                        xhr.setRequestHeader('Authorization',
                            "Basic " + localStorage.getItem('jwt'));
                  },
                },
                "columns": [
                    { "data": "tadp_code" },
                    { "data": "tadp_name" },
                ]
            });

             $(document).on('click','#detail',function () {
                handleClickOpen()
                setId($(this).attr('data-id'))
            })

             $(document).on('click','#edit',function () {
                handleClickOpen2()
                setId($(this).attr('data-id'))
                // setStep($(this).attr('data-step'))
            })

             $('.dataTables_filter,.dataTables_paginate').addClass('pull-right').css('float', 'right')
         })
         
     })

    return(
        <>
        <EditUser open={open2} close={handleClose2} id={id}/>
        <DetailUser open={open} close={handleClose} id={id}/>
            <Portlet>
                <PortletHeader title="Drop Point Terdaftar"/>
                <PortletBody>
                    <table className="display table table-hover table-responsive-md" id="dp_terdaftar" style={{width:'100%'}}>
                        <thead className="bg-primary text-center" style={{color:'white'}}>
                            <tr>
                                <th scope="col">Kode Drop Point</th>
                                <th scope="col">Nama Drop Point</th>
                            </tr>
                        </thead>
                    </table>
                </PortletBody>
            </Portlet>
            <Portlet>
                <PortletHeader title="Upload Data"toolbar={( 
                    <PortletHeaderToolbar>
                        <a href={TemplateUploadData} className="btn btn-label-primary">
                            <i className="fas fa-file-download"></i>Download Template
                        </a>
                    </PortletHeaderToolbar>
                )}/>
                <PortletBody>
                    <div className="input-group">
                        <select className="custom-select" id="inputGroupSelect04" style={{width:'10px'}} aria-label="Example select with button addon">
                            <option selected>Choose...</option>
                            <option value="1">Gantikan Data</option>
                            <option value="2">Tambahkan Data</option>
                            <option value="3">Hapus Data</option>
                        </select>
                        <div className="custom-file">
                            <input type="file" className="custom-file-input" id="inputGroupFile02"/>
                            <label className="custom-file-label" for="inputGroupFile02" aria-describedby="inputGroupFileAddon02">Pilih file</label>
                        </div>
                        <div className="input-group-append">
                            <button className="btn btn-outline-secondary" type="button">Upload</button>
                        </div>
                    </div>
                </PortletBody>
            </Portlet>
        </>
    )
}