/* eslint-disable no-restricted-imports */
import React, {useEffect} from 'react';
import $ from 'jquery';
import 'datatables.net-bs4';
import {
    Portlet,
    PortletBody,
    PortletHeader,
  } from "../../partials/content/Portlet";
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
// import InputAdornment from '@material-ui/core/InputAdornment'
import SearchIcon from '@material-ui/icons/Search';
import { makeStyles } from '@material-ui/core/styles';
import API from '../API'
import Utils from './Utils/FunctionDetail'
import DetailKendaraan from './Utils/DetailKendaraan'

const useStyles = makeStyles(theme => ({
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: '95%',
    },
    dense: {
      marginTop: 19,
    },
}));

export default function KendaraanAktif() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [id, setId] = React.useState(null);

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClose() {
        setOpen(false);
    }

     useEffect(() => {
         $(document).ready(function () {
                
                 $(document).on('click','#detail',function () {
                     handleClickOpen()
                     setId($(this).attr('data-id'))
                 })
         })
     })

     function FilterKendaraan() {
        if ($('#nopol').val() == 0) {
            return 0;
        }
        var api
        if( localStorage.getItem('role') == 5){ // internal admin
           api = API.Uploadapi+"v1/Data_kendaraan_aktif/index?nopol=" ;
         }else{
           api = API.Uploadapi+"v1/Data_kendaraan_aktif_leasing/index?nopol=" ;
         }

        console.log('cari');
        $('#kendaraan_aktif').DataTable({
            "pagingType": "full_numbers",
            "bFilter": false,
            "processing": true,
            "serverSide": true,
            "destroy": true,
            "ajax": {
                "url": api+$('#nopol').val(),
                "type": 'get',
                "beforeSend": function (xhr) {
                    xhr.setRequestHeader('Authorization',
                        "Basic " + localStorage.getItem('jwt'));
                },
                error: function (xhr, error, thrown, settings) {
                   //  errorData(xhr);
                }
            },
            "columns": [{
                    sortable: true,
                    "render": function (data, type, full, meta) {
                        return "<div>" + Utils.StatusUser(full.tvec_status) + " </div>";
                    }
                },
                {
                    "data": "tvec_vplate"
                },
                {
                    "data": "tvec_vtype"
                },
                {
                    "data": "tvec_vbrand"
                },
                {
                    "data": "tvec_ptype"
                },
                {
                    "data": "tvec_vyear"
                },
                {
                    "data": "tvec_vcolor"
                },
                {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                        return "<button class='detail btn btn-outline-brand btn-icon btn-sm' id='detail' data-id=" + full.tvec_id + " " +
                            " data-original-title='Detail Data Kendaraan'> <i class='fa fa-eye'> </i> </button>";
                    }
                },
            ]
        });
        $('.dataTables_filter,.dataTables_paginate').addClass('pull-right')
     }

    return(
        <>  
            <DetailKendaraan open={open} close={handleClose} id={id}/> 
            <Portlet>
            <PortletHeader
                title="Cari Nomor Polisi"
            >
            </PortletHeader>
                <PortletBody>
                <Grid container spacing={0} alignItems="flex-end">
                    <Grid item xs>
                    </Grid>
                    <Grid item>
                        <SearchIcon />
                    </Grid>
                    <Grid item xs={3}>
                        <TextField
                            id="nopol"
                            label="Masukan Nomor Polisi"
                            placeholder="Cth: B1234ABC"
                            className={classes.textField}
                            // margin="normal"
                        />
                    </Grid>
                    <Grid item xs={1}>
                        <button type="button" class="btn btn-brand btn-elevate btn-sm" onClick={FilterKendaraan} id="cari">Cari</button>
                    </Grid>
                    <Grid item xs>
                    </Grid>
                </Grid>
                </PortletBody>
            </Portlet>
            <Portlet>
                <PortletHeader
                    title="Data Kendaraan Aktif"
                >
                </PortletHeader>
                <PortletBody>
                    <table className="table table-hover table-responsive-md" id="kendaraan_aktif" style={{width:'100%'}}>
                            <thead className="bg-primary" style={{color:'white'}}>
                                <tr>
                                <th scope="col">Status</th>
                                <th scope="col">Nomor Polisi</th>
                                <th scope="col">Jenis Kendaraan</th>
                                <th scope="col">Merk</th>
                                <th scope="col">Tipe</th>
                                <th scope="col">Tahun</th>
                                <th scope="col">Warna</th>
                                <th scope="col">Aksi</th>
                                </tr>
                            </thead>
                    </table>
                </PortletBody>
            </Portlet>
        </>
    )
}