const ColorlibConnector = withStyles({
    alternativeLabel: {
      top: 35,
    },
    active: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,#183c7a 0%,#972127 50%,#ff0000 100%)',
      },
    },
    completed: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,#153264 100%)',
      },
    },
    line: {
      height: 3,
      border: 0,
      backgroundColor: '#eaeaf0',
      borderRadius: 1,
    },
  })(StepConnector);

const useColorlibStepIconStyles = makeStyles({
    root: {
      backgroundColor: '#ccc',
      zIndex: 1,
      color: '#fff',
      width: 70,
      height: 70,
      display: 'flex',
      borderRadius: '50%',
      justifyContent: 'center',
      alignItems: 'center',
    },
    active: {
      backgroundImage:
        'linear-gradient( 136deg, #ff0000 0%, #ff0000 50%, #ff0000 100%)',
      boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
      backgroundImage:
        'linear-gradient( 136deg, #183c7a 0%, #183c7a 50%, #183c7a 100%)',
    },
  });
  
  function ColorlibStepIcon(props) {
    const classes = useColorlibStepIconStyles();
    const { active, completed } = props;
  
    const icons = {
      1: <SettingsIcon fontSize="large" color="secondary"/>,
      2: <CloseIcon fontSize="large" />,
      3: <AssignmentTurnedInIcon fontSize="large"/>,
      4: <DoneAllIcon fontSize="large"/>,
    };
  
    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active,
          [classes.completed]: completed,
        })}
      >
        {completed ? <Check className={classes.completed} fontSize="large"/> : icons[String(props.icon)]}
        
      </div>
    );
  }
  
  ColorlibStepIcon.propTypes = {
    active: PropTypes.bool,
    completed: PropTypes.bool,
    icon: PropTypes.node,
  };

function getSteps() {
    return ['Menunggu Persetujuan', 'Voucher Ditolak','Voucher Diapprove', 'Voucher Sudah Lunas'];
  }

export default function VoucherDireject() {
    // const classes = useStyles();

    const [activeStep] = React.useState(1);
    const steps = getSteps();

     useEffect(() => {
        $(document).ready(function () {            
              $('#voucher-reject').DataTable({
                "pagingType": "full_numbers",
                "processing": true,
                "serverSide": true,
                "destroy": true,
                "ajax": {
                  "url": API.Financeapi + "v1/Voucher_ditolak/index",
                  "type": 'get',
                  "beforeSend": function (xhr) {
                    xhr.setRequestHeader('Authorization',
                      "Basic " + localStorage.getItem('jwt'));
                  },
                  error: function (xhr, error, thrown, settings) {
                    // errorData(xhr);
                  }
                },
                "fnRowCallback": function (nRow, aData, iDisplayIndex, iDisplayIndexFull) {
                  if (aData.tvoc_status == "Voucher sudah diisi") {
                    $('td:eq(8)', nRow).css('background-color', '#81D4FA');
                  } else if (aData.tvoc_status == "Siap Dibayar") {
                    $('td:eq(8)', nRow).css('background-color', '#FFE800');
                  } else if (aData.tvoc_status == "Ditolak") {
                    $('td:eq(8)', nRow).css('background-color', '#FD333F');
                  } else if (aData.tvoc_status == "Telah Dibayar") {
                    $('td:eq(8)', nRow).css('background-color', '#6CFF00');
                  }
                  if (aData.tvoc_type == "Referall") {
                    $('td:eq(7)', nRow).css('background-color', '#9999ff');
                  }
                  if (aData.app_type == "mobil") {
                    $('td:eq(0)', nRow).css('background-color', '#AFEEEE');
                  } else if (aData.app_type == "motor") {
                    $('td:eq(0)', nRow).css('background-color', '#F4A460');
                  }
                },
                "columns": [{
                    "data": "app_type"
                  },
                  {
                    "data": "lvoc_created_date"
                  },
                  {
                    "data": "lvoc_created_time"
                  },
                  {
                    "data": "tvoc_account_name"
                  },
                  {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                      // return " <div >" + formatter.format(parseInt(full.tvoc_ammount)) + "</div>";
                    }
                  },
                  {
                    "data": "tvoc_vehicle_number"
                  },
                  {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                      return " <div > ( " + full.tpnm_vtype + " ) " + full.tpnm_type + " " + full.tpnm_vyear + " " + full.tpnm_vcolor + "</div>";
                    }
                  },
                  {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                      return " <div >" + full.tvoc_type + "</div>";
                    }
                  },
                  {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                      return " <div>" + full.tvoc_status + "</div>";
                    }
                  },
                  {
                    sortable: true,
                    "render": function (data, type, full, meta) {
                      return "<button class='details btn yellow-gold btn-xs btn-outline tooltips' id='details' data-type=" + full.tvoc_type + " data-id=" + full.tpnm_id + " " +
                        " data-original-title='Detail Data Penemuan'> <i class='fa fa-eye'> </i> </button>";
                    }
                  },
                ]
              });
                    $('.dataTables_filter,.dataTables_paginate').addClass('pull-right')
                })
     })
    return(
        <>              
            <Portlet>
                <PortletHeader
                    title="Step Voucher"
                >
                </PortletHeader>
                <PortletBody>
                    <Grid container spacing={3}>
                        <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />} style={{width:'100%'}}>
                            {steps.map(label => (
                            <Step key={label}>
                                <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
                            </Step>
                            ))}
                        </Stepper>
                    </Grid>
                </PortletBody>
            </Portlet>
            <Portlet>
                <PortletBody>
                    <table className="table table-hover table-responsive-md" id="voucher-reject" style={{width:'100%'}}>
                            <thead className="bg-primary" style={{color:'white'}}>
                                <tr>
                                <th scope="col">Tipe</th>
                                <th scope="col">Tanggal</th>
                                <th scope="col">Jam</th>
                                <th scope="col">Nama</th>
                                <th scope="col">Harga</th>
                                <th scope="col">Nopol</th>
                                <th scope="col">Unit</th>
                                <th scope="col">Tipe Voucher</th>
                                <th scope="col">Status</th>
                                <th scope="col">Aksi</th>
                                </tr>
                            </thead>
                    </table>
                </PortletBody>
            </Portlet>
        </>
    )
}