/* eslint-disable no-restricted-imports */
import React, {useEffect} from 'react';
import $ from 'jquery';
import PropTypes from 'prop-types';
import 'datatables.net-bs4';
import {
    Portlet,
    PortletBody,
    PortletHeader,
  } from "../../partials/content/Portlet";
import Grid from '@material-ui/core/Grid';
import { makeStyles, withStyles  } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import Check from '@material-ui/icons/Check';
import AssignmentIcon from '@material-ui/icons/Assignment';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import DoneAllIcon from '@material-ui/icons/DoneAll';
import API from '../API'
import DetailVoucherVerifikasi from './Utils/DetailVoucherVerfikasi'

const useStyles = makeStyles(theme => ({
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: '100%',
    },
    dense: {
      marginTop: 19,
    },
}));

const ColorlibConnector = withStyles({
    alternativeLabel: {
      top: 35,
    },
    active: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,#153264 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
      },
    },
    completed: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,#153264 100%)',
      },
    },
    line: {
      height: 3,
      border: 0,
      backgroundColor: '#eaeaf0',
      borderRadius: 1,
    },
  })(StepConnector);

const useColorlibStepIconStyles = makeStyles({
    root: {
      backgroundColor: '#ccc',
      zIndex: 1,
      color: '#fff',
      width: 70,
      height: 70,
      display: 'flex',
      borderRadius: '50%',
      justifyContent: 'center',
      alignItems: 'center',
    },
    active: {
      backgroundImage:
        'linear-gradient( 136deg, #153264 0%, #153264 50%, #153264 100%)',
      boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
      backgroundImage:
        'linear-gradient( 136deg, #183c7a 0%, #183c7a 50%, #183c7a 100%)',
    },
  });
  
  function ColorlibStepIcon(props) {
    const classes = useColorlibStepIconStyles();
    const { active, completed } = props;
  
    const icons = {
      1: <AssignmentIcon fontSize="large" color="secondary"/>,
      2: <AssignmentTurnedInIcon fontSize="large"/>,
      3: <DoneAllIcon fontSize="large"/>,
    };
  
    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active,
          [classes.completed]: completed,
        })}
      >
        {completed ? <Check className={classes.completed} fontSize="large"/> : icons[String(props.icon)]}
        
      </div>
    );
  }
  
  ColorlibStepIcon.propTypes = {
    active: PropTypes.bool,
    completed: PropTypes.bool,
    icon: PropTypes.node,
  };

function getSteps() {
    return ['Menunggu Persetujuan', 'Voucher Diapprove', 'Voucher Sudah Lunas'];
  }

export default function VoucherVerifikasi() {

    // eslint-disable-next-line no-unused-vars
    const classes = useStyles();
    // eslint-disable-next-line no-unused-vars
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps();
    const [open, setOpen] = React.useState(false);
    const [id, setId] = React.useState(null);
    // eslint-disable-next-line no-unused-vars
    const [tvoc, setTvoc] = React.useState(null);
    const [tvoctype, setTvoctype] = React.useState(null);

     useEffect(() => {
       $(document).ready(function () {
         $('#voucher-main').DataTable({
           "pagingType": "full_numbers",
           "aaSorting": [],
           "processing": true,
           "serverSide": true,
           "destroy": true,
           "ajax": {
             "url": API.Financeapi + "v1/Voucher_verifikasi/index",
             "type": 'get',
             "beforeSend": function (xhr) {
               xhr.setRequestHeader('Authorization',
                 "Basic " + localStorage.getItem('jwt'));
             },
             error: function (xhr, error, thrown, settings) {
               // errorData(xhr);
             }
           },
           "fnRowCallback": function (nRow, aData, iDisplayIndex, iDisplayIndexFull) {
             console.log(aData.tvoc_type);
             if (aData.tvoc_status == "Voucher sudah diisi") {
               $('td:eq(9)', nRow).css('background-color', '#81D4FA');
             } else if (aData.tvoc_status == "Siap Dibayar") {
               $('td:eq(9)', nRow).css('background-color', '#FFE800');
             } else if (aData.tvoc_status == "Ditolak") {
               $('td:eq(9)', nRow).css('background-color', '#FD333F');
             } else if (aData.tvoc_status == "Telah Dibayar") {
               $('td:eq(9)', nRow).css('background-color', '#6CFF00');
             }
             if (aData.tvoc_type == "Referall") {
               $('td:eq(8)', nRow).css('background-color', '#9999ff');
             }
             if (aData.app_type == "mobil") {
               $('td:eq(0)', nRow).css('background-color', '#AFEEEE');
             } else if (aData.app_type == "motor") {
               $('td:eq(0)', nRow).css('background-color', '#F4A460');
             }
           },
           "columns": [{
               "data": "app_type"
             },
             {
               "data": "tvoc_created_date"
             },
             {
               "data": "tvoc_created_time"
             },
             {
               "data": "tvoc_account_name"
             },
             {
               sortable: true,
               "render": function (data, type, full, meta) {
                 if (full.tvoc_status_stnk == 1) {
                   // full.tvoc_ammount = formatter.format(parseInt(full.tvoc_fee_stnk) + parseInt(full.tvoc_ammount));
                 } else {
                   // full.tvoc_ammount = formatter.format(parseInt(full.tvoc_ammount));
                 }
                 console.log("| " + full.tvoc_ammount);
                 return " <div >" + full.tvoc_ammount + "</div>";
               }
             },
             {
               "data": "tvoc_vehicle_number"
             },
             {
               "data": "mmle_name"
             },
             {
               sortable: true,
               "render": function (data, type, full, meta) {
                 return " <div > ( " + full.tpnm_vtype + " ) " + full.tpnm_type + " " + full.tpnm_vyear + " " + full.tpnm_vcolor + "</div>";
               }
             },
             {
               sortable: true,
               "render": function (data, type, full, meta) {
                 return " <div >" + full.tvoc_type + "</div>";
               }
             },
             {
               sortable: true,
               "render": function (data, type, full, meta) {
                 return " <div >" + full.tvoc_status + "</div>";
               }
             },
             {
               sortable: true,
               "render": function (data, type, full, meta) {
                 return "<button class='details btn btn-outline-brand btn-icon btn-sm' id='details' data-type=" + full.tvoc_type + " data-id=" + full.tpnm_id + " data-tvoc=" + full.tvoc_id +
                   " data-original-title='Detail Data Penemuan'> <i class='fa fa-eye'> </i> </button>";
               }
             },
           ]
         });
         $(document).on('click', '#details', function () {
           handleClickOpen()
           setId($(this).attr('data-id'))
           setTvoc($(this).attr('data-tvoc'))
           setTvoctype($(this).attr('data-type'))
           console.log(id)
         })

         $('.dataTables_filter,.dataTables_paginate').addClass('pull-right')
       })
     })

     function handleClickOpen() {
       setOpen(true);
     }

     function handleClose() {
       setOpen(false);
     }

    return(
        <>              
            <DetailVoucherVerifikasi open={open} close={handleClose} id={id} step={6} tvoctype={tvoctype} tvoc={tvoc}/>
            <Portlet>
                <PortletHeader
                    title="Step Voucher"
                >
                </PortletHeader>
                <PortletBody>
                    <Grid container spacing={3}>
                        <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />} style={{width:'100%'}}>
                            {steps.map(label => (
                            <Step key={label}>
                                <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
                            </Step>
                            ))}
                        </Stepper>
                    </Grid>
                </PortletBody>
            </Portlet>
            <Portlet>
                <PortletHeader title="Data Voucher Verifikasi"/>
                <PortletBody>
                    <table className="table table-hover table-responsive-md" id="voucher-main" style={{width:'100%'}}>
                            <thead className="bg-primary" style={{color:'white'}}>
                                <tr>
                                <th scope="col">Tipe</th>
                                <th scope="col">Tanggal</th>
                                <th scope="col">Jam</th>
                                <th scope="col">Nama</th>
                                <th scope="col">Harga</th>
                                <th scope="col">Nopol</th>
                                <th scope="col">Leasing</th>
                                <th scope="col">Unit</th>
                                <th scope="col">Tipe Voucher</th>
                                <th scope="col">Status</th>
                                <th scope="col">Aksi</th>
                                </tr>
                            </thead>
                    </table>
                </PortletBody>
            </Portlet>
        </>
    )
}